import "./catalog.scss";
import catalogMenu from "../../js/components/catalog-menu";

let switchProductVIew = () => {
    const $blockBtn = $("#block-btn");
    const $listBtn = $("#list-btn");
    const $tovarList = $("#tovar-list");

    $listBtn.off("click");
    $listBtn.on("click", function(){
        $blockBtn.removeClass("active");
        $(this).addClass("active");
        $tovarList.addClass("list-view");
    });
    
    $blockBtn.off("click");
    $blockBtn.on("click", function(){
        $listBtn.removeClass("active");
        $tovarList.removeClass("list-view");
        $(this).addClass("active");
    });

};

$(window).on("load", function () {
    catalogMenu();
    switchProductVIew();
});



